.error-main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  height: 100vh;
}

.error-text {
  padding: 20px 0;
  font-size: 24px;
  color: #333;
  opacity: 0.8;
  top: calc(100% - 50% - 177px);
  @media (max-width: 1024px) {
    font-size: 13px;
  }
}

.error-image-div {
  @media (max-width: 1024px) {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  img {
    @media (max-width: 1024px) {
      width: 60%;
    }
  }
}
